import './Playground.css';
import AnimatedCharacter from '../../components/AnimatedCharacter/AnimatedCharacter';
import TopBar from '../../components/TopBar/TopBar';
import WordTyping from '../../components/WordTyping/WordTyping';
import { useParams } from 'react-router-dom';
import { useState } from 'react';
import wordData from '../../data/words.json';

function Playground() {
  const { stageId } = useParams<{ stageId: string }>();
  const stageIdNumber = parseInt(stageId || '1', 10); // stageIdを数値に変換
  // JSONデータから該当するステージの単語リストを取得
  const stageData = wordData.find((stage) => stage.stageId === stageIdNumber);
  const stageWords = stageData ? stageData.words : []; // 該当するステージがない場合は空配列
  const [progress, setProgress] = useState(0);

  // ステータスを増加させる関数
  const handleCorrectAnswer = () => {
    setProgress((prev) => Math.min(prev + 1, 10)); // 最大10まで
  };

  return (
    <div className="app-container">
      <TopBar />
      <h1>Playing Stage {stageId}</h1>
      <div className="status-bar-container">
        <div className="status-bar">
          <div
            className="status-bar-progress"
            style={{ width: `${(progress / 10) * 100}%` }} // 進捗を%に変換
          ></div>
        </div>
        <span className="status-text">{progress} / 10</span>
      </div>
      <header className="main-content">
        <AnimatedCharacter />
        <WordTyping stageWords={stageWords} onCorrectAnswer={handleCorrectAnswer} /> {/* コールバックを渡す */}
      </header>
    </div>
  );
}

export default Playground;
